html, body, .app, #root, .app-layout {
  width: 100%;
  height: 100%;
  direction: rtl;
  overflow: hidden;
  font-family: Heebo;
}

* {
  box-sizing: border-box;
}

.basad {
  position: absolute;
  top: 0;
  right: 0;
  color: white;
  font-size: 1.2em;
  font-weight: bold;
  margin: 1em;
}

.app-bg {
  position: absolute;
  background-color: #DE9433;
  width: 100%;
  height: 100%;
}

.finally {
  font-weight: bold;
}

.bg-image {
  background-image: url('static/img/bg_image.png');
}

.bg-image, .bg-image-fade {
  position: absolute;
  height: 60%;
  width: 100%;
  background-size: cover;
  background-position: bottom;
}

.bg-image-fade {
  position: absolute;
  height: 60%;
  width: 100%;
  background-size: cover;
}

.app-layout {
  position: absolute;
  display: grid;
}

.logo-image, .logo-name {
  background-position-y: bottom;
  background-repeat: no-repeat;
  background-size: contain;
}

.logo-image {
  background-image: url('img/logo_image.png');
}
.logo-name {
  background-image: url('img/logo_text.png');
}

$breakpoint: 900px;

@media only screen and (min-width: $breakpoint) {
  .bg-image-fade {
    background: linear-gradient(transparent 50%, black);
  }

  .app-layout {
    grid-template: ". .    .    .   ." 140fr
                   ". .    .    app ." 200fr
                   ". logo tag  app ." 285fr
                   ". .    desc app ." 200fr
                   ". .    .    .   ." 220fr / 10fr 3fr 10fr 5.3fr 10fr;
  }

  .logo-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  .logo-image, .logo-name {
    width: 10em;
  }

  .tagline {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-start;
    padding-bottom: .4em;
  }

  .tagline, .description {
    padding-right: 5vh;
    padding-left: 3vh;
  }

  .tagline, .description-text {
    font-size: 2.5vh;
  }

  .phone {
    align-items: flex-start;
  }

  .phone {
    width: 100%;
    height: 100%;
  }

  .description {
    align-items: flex-start;
  }


  .logo-image {
    flex: 4;
  }

  .logo-name {
    flex: 1;
  }

  .store-buttons {
    height: 6vh;
  }

  .phone .phone-reflection {
    position: absolute;
    height: 100%;
    width: 100%;
  }

  .logo-container, .tagline {
    overflow: hidden;
  }
}

@media only screen and (max-width: $breakpoint) {
  .bg-image-fade {
    background: linear-gradient(black, transparent 50%);
  }

  .app-layout {
    $margin: 3fr;
    grid-template: "." 3vh
                   "logo" 160fr
                   "." 3vh
                   "tag" 90fr
                   "." 3vh
                   "app" 670fr
                   "desc" 160fr
                   "." 2vh / 1fr;
    overflow-y: scroll;
  }

  .store-buttons {
    height: 6vh;
  }

  .tagline {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }

  .tagline, .description-text {
    font-size: 2.5vh;
  }

  .logo-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .logo-image {
    background-position: center;
    height: 5em;
    width: 4em;
  }

  .logo-name {
    background-position: right;
    height: 2.25em;
    width: 6em;
    margin-top: 2em;
    margin-right: 1em;
  }

  .phone {
    align-items: center;
  }

  .description {
    align-items: center;
  }

  .phone-preview {
    width: 95%;
    height: auto;
  }

  .phone-reflection {
    display: none;
  }
}

.logo-container {
  grid-area: logo;
}

.logo-image {
  max-height: 100%;
  object-fit: contain;
}

.tagline {
  grid-area: tag;
  color: white;
  text-align: center;
  white-space: nowrap;
}

.description {
  grid-area: desc;
  width: 100%;
  height: 100%;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  z-index: 1;
  animation-name: description-enter;
  animation-fill-mode: backwards;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-timing-function: ease-out;
}

.description-text {
  margin-top: .5em;
  margin-bottom: .5em;
  white-space: nowrap;
}

.store-buttons {
  display: flex;
  flex-direction: row;
  overflow: hidden;
  align-items: stretch;
}

.store-buttons * {
  cursor: pointer;

  &.disabled {
    filter: saturate(0);
    cursor: not-allowed;
  }
}

.store-buttons *:not(:first-child) {
  margin-right: .5em;
}

.phone {
  grid-area: app;
}

@keyframes description-enter {
  from {
    opacity: 0;
    transform: translateY(2em);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes phone-enter {
  from {
    transform: translateY(-2em);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes phone-reflection-enter {
  from {
    transform: translateY(110%);
    opacity: 0;
  }

  to {
    transform: translateY(105%);
    opacity: 1;
  }
}

.phone {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.phone-preview {
  animation-name: phone-enter;
  animation-timing-function: ease-out;
  animation-duration: 1s;
}

.phone-reflection {
  transform: translateY(105%);
  animation-name: phone-reflection-enter;
  animation-timing-function: ease-out;
  animation-duration: 1s;
}

.thanks {
  width: 100%;
  max-height: 100%;
  background-color: #adc467 !important;
  padding: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #233423 !important;

  .thanks-icon {
    margin-left: .5em;
    height: 1em;
    width: 1em;
  }

  @media only screen and (max-width: $breakpoint) {
    & {
      margin-left: 2em;
      margin-right: 2em;
      flex-grow: 1;
    }
  }
}

.spots {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 1em 1em 0;
  color: #7c2838;
}

.spots-text {
  font-size: 2em;
  font-weight: bold;
}

.spots-label {
  font-size: 1em;
}